.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1;
}
.Overlay div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.Overlay img {
  width: 1100px;
  height: 600px;
}
